module.exports = {"title":"Farol do Pontal de Peggys, Canadá","content":"Talvez o farol mais famoso do Canadá, o atual Farol do Pontal de Peggys foi aceso pela primeira vez em 1915. Ele marca a entrada leste da Baía de St. Margaret e é uma das atrações turísticas mais populares da Nova Escócia.","figure":{"alt":"Foto do Farol do Pontal de Peggys","credits":"Foto: Dennis Jarvis"}}

